import React from "react";
import { graphql } from "gatsby";
import Layout from "../components/layout";
import SEO from '../components/seo';

const Post = (props) => {

  console.log(props)
  const post = props.data.post

    return (
        <Layout>
          <SEO
            post={{
              ...post,
              image: post.thumbnail_url || false,
            }}
          />
          <div className="m-auto main">
            <div className="bg-white my-2">
                <article className="p-4 border-bottom">
                    <h2 className="fs-5">{post.title}</h2>

                    <div className="pt-3">
                      {post.content}
                    </div>

                </article>
            </div>
          </div>
        </Layout>
    )
  }

export default Post

export const query = graphql`
  query tagsGetPostQuery($slug: String!) {
    post(slug: {eq: $slug}) {
      title
      content
      created_at
    }
  }
`

